import React, { Component } from "react";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import MetaTags from "react-meta-tags";
import { logout, getUserData } from "../../actions/userActions";
import { getMerchant } from "../../actions/merchantActions";
import MerchantStoreButton from "../profile/merchant/js/MerchantStoreButton";
import "./HeaderStyle.css";

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      search: "",
      isOpen: false,
    };
    this.logout = this.logout.bind(this);
    this.getUserAndMerchantData = this.getUserAndMerchantData.bind(this);
  }

  onChange = (event) => {
    this.setState({ search: event.target.value });
  };

  submitSearch = (event) => {
    event.preventDefault();
    console.log(this.state.search);
    this.props.passSearchData(this.state.search);
  };

  toggle = () => {
    this.setState({
      isOpen: !this.state.isOpen,
    });
  };

  getUserAndMerchantData = (id) => {
    this.props.getUserData(id);
    this.props.getMerchant(id);
  };

  logout = () => {
    this.props.logout();
    window.location.href = "/home";
  };

  render() {
    const { validToken, user } = this.props.user;

    const userIsNotAuthenticated = (
      <div className="navbar-button search-field">
        <ul className="navbar-nav-acc">
          <li className="nav-item-si">
            <Link to="/login" className="dropdown-item nav-link">
              LOG IN
            </Link>
          </li>
          <li className="nav-item-su">
            <Link to="/register" className="dropdown-item nav-link">
              SIGN UP
            </Link>
          </li>
        </ul>
      </div>
    );

    const userIsAuthenticated = (
      <div>
        <ul className="navbar-nav-acc ml-4 user-authentic">
          <li className="nav-item">
            <Link to="/cart" className="nav-link">
              Cart
            </Link>
          </li>
          <li className="nav-item">
            <Link to="/invoice" className="nav-link">
              Invoice
            </Link>
          </li>
          <div class="dropdown-divider"></div>
          <li className="nav-item">
            <Link
              to={`/profile/${user.username}`}
              className="nav-link"
              onClick={() => this.getUserAndMerchantData(user.id)}
            >
              <MerchantStoreButton user={user} />
            </Link>
          </li>
          <div class="dropdown-divider"></div>
          <li className="nav-item logout">
            <Link to="/home" className="nav-link" onClick={this.logout}>
              Logout
            </Link>
          </li>
        </ul>
      </div>
    );

    let headerLinks;

    // const userProfileAuthenticated = (
    //   <div class="dropdown option user-profile-authentic">
    //     <a class="nav-link dropdown-toggle" href="#" id="navbarDropdownMenuLink" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
    //       {user.username}
    //     </a>
    //       <ul className="dropdown-menu">
    //         <li className="nav-item be-merch-comp">
    //             <MerchantStoreButton user={user} />
    //         </li>
    //       </ul>
    // </div>
    // );

    // const userProfileIsNotAuthenticated = (
    //     <div></div>
    // );

    // let userProfileShown;

    if (validToken && user) {
      headerLinks = userIsAuthenticated;
    } else {
      headerLinks = userIsNotAuthenticated;
    }

    // if (validToken && user) {
    //   userProfileShown = userProfileAuthenticated;
    // } else {
    //   userProfileShown = userProfileIsNotAuthenticated;
    // }

    return (
      <React.Fragment>
        <MetaTags>
          <meta charSet="UTF-8" />
          <meta
            name="viewport"
            content="width=device-width, initial-scale=1.0"
          />
        </MetaTags>

        <nav className="navbar navbar-expand-lg navbar-light bg-light">
          <Link to="/home" className="navbar-brand" data-abc="true">
            <img
              className="logo"
              src={require("./images/header/logo.png")}
              alt=""
            ></img>
          </Link>

          <button
            className="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#navbarNavDropdown"
            aria-controls="navbarNavDropdown"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarNavDropdown">
            <ul className="navbar-nav">
              <li>
                <div className="search-menu">
                  <input
                    className="search"
                    type="text"
                    placeholder="Search"
                    onChange={this.onChange.bind(this)}
                  />
                  <Link
                    to="/"
                    className="btn"
                    type="submit"
                    onClick={this.submitSearch.bind(this)}
                  >
                    Search
                  </Link>
                </div>
              </li>
              <li class="nav-item option">
                <Link to="/home" class="nav-link">
                  Home <span class="sr-only">(current)</span>
                </Link>
              </li>
              <li class="nav-item option">
                <Link to="/catalog" class="nav-link">
                  Catalog
                </Link>
              </li>
              <li class="nav-item dropdown option">
                <a
                  class="nav-link dropdown-toggle"
                  href="#"
                  id="navbarDropdownMenuLink"
                  role="button"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  Account
                </a>
                <div
                  class="dropdown-menu account-dropdown"
                  aria-labelledby="navbarDropdownMenuLink"
                >
                  {headerLinks}
                </div>
              </li>
            </ul>
          </div>
        </nav>
      </React.Fragment>
    );
  }
}

Header.propTypes = {
  getUserData: PropTypes.func.isRequired,
  logout: PropTypes.func.isRequired,
  user: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  user: state.user,
});

const mapDispatchToProps = (dispatch) => {
  return {
    logout: () => {
      dispatch(logout());
    },
    getUserData: (id) => {
      dispatch(getUserData(id));
    },
    getMerchant: (id) => {
      dispatch(getMerchant(id));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Header);
