import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { withRouter } from "react-router-dom";
import classnames from "classnames";
import {
  createProduct,
  uploadImage,
} from "../../../../actions/merchantActions.js";
import { getCategory } from "../../../../actions/categoryActions";
import "../css/AddProduct.css";

class AddProduct extends Component {
  constructor(props) {
    super(props);
    this.state = {
      imagePreview: null,
      file: null,
      productName: "",
      productCategoryName: "",
      productDesciption: "",
      productPrice: "",
      productStock: "",
      productImage: null,
      errors: {},
    };
    this.handleChange = this.handleChange.bind(this);
    this.submitProduct = this.submitNewProduct.bind(this);
    this.handleUploadClick = this.handleUploadClick.bind(this);
    this.uploadImageWithAdditionalData = this.uploadImageWithAdditionalData.bind(
      this
    );
  }

  componentDidMount() {
    this.props.getCategory();
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
  }

  handleUploadClick = (event) => {
    let file = event.target.files[0];
    this.setState({
      file: file,
      imagePreview: URL.createObjectURL(file),
    });
  };

  uploadImageWithAdditionalData = (event) => {
    event.preventDefault();
    const merchantID = this.props.merchant.merchant.id;
    const formData = new FormData();
    formData.append("productCategoryName", this.state.productCategoryName);
    formData.append("productName", this.state.productName);
    formData.append("productDescription", this.state.productDescription);
    formData.append("file", this.state.file);

    this.props.uploadImage(merchantID, formData);
  };

  handleChange = (event) => {
    this.setState({ [event.target.name]: event.target.value });
  };

  submitNewProduct = (event) => {
    event.preventDefault();
    const newProduct = {
      productName: this.state.productName,
      productCategoryName: this.state.productCategoryName,
      productDescription: this.state.productDescription,
      productPrice: this.state.productPrice,
      productStock: this.state.productStock,
    };
    this.props.createProduct(
      this.props.merchant.merchant.id,
      newProduct,
      this.props.history
    );
  };

  render() {
    const { errors } = this.state;
    const { categories } = this.props.category;

    return (
      <React.Fragment>
        <div className="Add-Product-Content">
          <h1>Add New Product</h1>
          <form>
            <div className="productImages">
              <h2>Product Image</h2>
              <div className="productImageInput">
                <div className="inputImage">
                  <input
                    className="inputImageButton"
                    type="file"
                    id="upload-product-image"
                    accept="image/*"
                    onChange={this.handleUploadClick}
                  />
                  <h4 id="primaryImage">Primary Image</h4>
                </div>
                <div className="inputImage">
                  <img
                    src={
                      this.state.imagePreview !== null
                        ? this.state.imagePreview
                        : null
                    }
                  ></img>
                  <input
                    className="inputImageButton"
                    type="file"
                    id="img"
                    name="img"
                    accept="image/*"
                  ></input>
                </div>
                <div className="inputImage">
                  <input
                    className="inputImageButton"
                    type="file"
                    id="img"
                    name="img"
                    accept="image/*"
                  ></input>
                </div>
                <div className="inputImage">
                  <input
                    className="inputImageButton"
                    type="file"
                    id="img"
                    name="img"
                    accept="image/*"
                  ></input>
                </div>
                <div className="inputImage">
                  <input
                    className="inputImageButton"
                    type="file"
                    id="img"
                    name="img"
                    accept="image/*"
                  ></input>
                </div>
              </div>
            </div>

            <div className="input-add-product">
              <h2>Product Detail</h2>
              <div className="productNames">
                <p className="name-input">Product Name</p>
                <p id="equal-sign">=</p>
                <input
                  type="text"
                  className={classnames("form-control", {
                    "is-invalid": errors.productName,
                  })}
                  name="productName"
                  onChange={this.handleChange}
                />
                {errors.productName && (
                  <div className="invalid-feedback">{errors.productName}</div>
                )}
              </div>

              <div className="productCategory">
                <p className="category-input">Product Category</p>
                <p id="equal-sign">=</p>
                <select
                  className={classnames("", {
                    "is-invalid": errors.productCategoryName,
                  })}
                  value={this.state.productCategoryName}
                  name="productCategoryName"
                  onChange={this.handleChange}
                >
                  <option value="">--</option>
                  {categories.map((category) => (
                    <option
                      key={category.category_id}
                      value={category.categoryName}
                    >
                      {category.categoryName}
                    </option>
                  ))}
                </select>
                {errors.productCategoryName && (
                  <div className="invalid-feedback">
                    {errors.productCategoryName}
                  </div>
                )}
              </div>

              <div className="productDescriptions">
                <p className="description-input">Product Description</p>
                <textarea
                  className={classnames("text-area-add-product", {
                    "is-invalid": errors.productDescription,
                  })}
                  name="productDescription"
                  onChange={this.handleChange}
                  placeholder="Write your product description here..."
                />
                {errors.productDescription && (
                  <div className="invalid-feedback">
                    {errors.productDescription}
                  </div>
                )}
              </div>

              <div className="productPrices">
                <p className="price-input">Product Price</p>
                <p id="equal-sign">=</p>
                <p id="Curr">Rp.</p>
                <input
                  type="text"
                  className={classnames("form-control", {
                    "is-invalid": errors.productPrice,
                  })}
                  name="productPrice"
                  onChange={this.handleChange}
                />
                {errors.productPrice && (
                  <div className="invalid-feedback">{errors.productPrice}</div>
                )}
              </div>

              <div className="productStocks">
                <p className="stock-input">Product Stock</p>
                <p id="equal-sign">=</p>
                <input
                  type="text"
                  className={classnames("form-control", {
                    "is-invalid": errors.productStock,
                  })}
                  name="productStock"
                  onChange={this.handleChange}
                />{" "}
                <p>&nbsp;&nbsp;pcs</p>
                {errors.productStock && (
                  <div className="invalid-feedback">{errors.productStock}</div>
                )}
              </div>
            </div>

            <div className="ButtonAddProducts">
              <Link to="/my-shop/catalog">
                <button className="cancelButtonAP">Cancel</button>
              </Link>
              <button
                className="SaveAndAddNew"
                type="submit"
                onClick={this.submitNewProduct}
              >
                Save and Add New
              </button>

              <button
                className="Save"
                type="submit"
                onClick={this.uploadImageWithAdditionalData}
              >
                Save Product
              </button>
            </div>
          </form>
        </div>
      </React.Fragment>
    );
  }
}

AddProduct.propTypes = {
  user: PropTypes.object.isRequired,
  createProduct: PropTypes.func.isRequired,
  errors: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  user: state.user,
  category: state.category,
  merchant: state.merchant,
  errors: state.errors,
});

const mapDispatchToProps = (dispatch) => {
  return {
    getCategory: () => {
      dispatch(getCategory());
    },
    createProduct: (merchantID, newProduct, history) => {
      dispatch(createProduct(merchantID, newProduct, history));
    },
    uploadImage: (merchantID, formData) => {
      dispatch(uploadImage(merchantID, formData));
    },
  };
};

export default withRouter(
  connect(mapStateToProps, mapDispatchToProps)(AddProduct)
);
